<template>
    <header id='head_top'>
        <div class="topBar">
            <div class="container flex j-bet">
                <div class="greet">400-8383-5098:30-18:00</div>
                <div class="topMenu flex j-bet">
                    <div class="tm_item">
                        <router-link to="/home" @click.native="fresh"></router-link>
                    </div>
                    <div class="tm_item" @click="jumpLink('/cart/cart')">
                        <span>
                            <el-badge is-dot class="item" :hidden="cartList.length==0">
                                
                            </el-badge>
                        </span>
                    </div>
                    <div class="tm_item" v-if="!login">
                        <router-link to="/login/login"></router-link>
                    </div>
                    <div class="tm_item" v-if="login" >
                        <router-link to="/userCenter/index/index"></router-link>
                    </div>
                    <div class="tm_item" @click="jumpLink('/userCenter/message/message')">
                        <span>
                            <i class="el-icon-message ml5"></i>
<!--                            <el-badge is-dot class="item">-->
<!--                                <i class="el-icon-message ml5"></i>-->
<!--                            </el-badge>-->
                        </span>
                    </div>
                    <div v-if="login" class="tm_item" @click="outLogin()" >
                        <span></span>
                    </div>
                </div>
            </div>
        </div>
        <div class="navigation-wrap">
            <div class="container flex j-bet">
                <div class="logo-group flex f-nowrap">
                    <div class="logo">
                        <router-link to="/home" @click.native="fresh"><img src="../../images/logo.png"></router-link>
                    </div>
<!--                    <div class="logoTit"></div>-->
                </div>
                <div class="navMenu flex">
                    <div :class="webActive=='home'?'navItem pr active':'navItem pr'">
                        <router-link to="/home" @click.native="fresh"></router-link>
                        <label class="navLabel"></label>
                    </div>
                    <div :class="webActive=='mine'?'navItem active':'navItem'">
                        <router-link to="/userCenter/index/index"></router-link>
                    </div>
                    <div :class="webActive=='cart'?'navItem active':'navItem'">
                        <router-link to="/cart/cart">
                            <i class="el-icon-shopping-cart-1 color6 f16 mr5"></i>
                                
                            <span class="badge" v-if="cartList.length>0">{{cartList.length}}</span>
                        </router-link>
                    </div>
                    <div class="borderLine"></div>
                    <div :class="webActive=='help'?'navItem active':'navItem'">
                        
                       <a href="http://www.huaxuejia.cn" target="_blank" style="color:#0e79cc;">
                        <img style="width:18px;height:18px;" src="../../images/favicon.png">
                        <span></span>
                        
                       </a>
                    </div>
                    
<!--                    <div :class="webActive=='help'?'navItem active':'navItem'">-->
<!--                        <a href=""></a>-->
<!--                    </div>-->
                    <div class="navItemLast">
                        <span>400-8383-509</span>
                        <p>8:30-18:00</p>
                    </div>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
    import {mapState, mapMutations, mapActions} from 'vuex'
    import {signout} from '../../service/api';
    import{removeStore} from '../../config/mUtils';
    export default {
        name: 'headTop',
    	data(){
            return{
                menu:['home','mine','help'],
            }
        },
        props: ['webActive'],
        created(){
            //
            //this.getUserInfo();
        },
        beforeMount(){
            //
            //this.getUserInfo();
        },
        mounted(){
            this.getUserInfo();
            this.initCart();
            
        },
        computed: {
            ...mapState([
                'userInfo','cartList','login'
            ])
        },
        methods:{
            ...mapMutations([
                'OUT_LOGIN'
            ]),
            ...mapActions([
                'getUserInfo','initCart'
            ]),
            async outLogin(){
                this.$confirm('?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(async ()=> {
                    var res = await signout();
                    if(res.status == 12000){
                        this.$message({
                            message:res.msg,
                            type:'success'
                        });
                        this.OUT_LOGIN();
                        this.initCart();
                        removeStore('user_id');
                    }else{
                        this.$message({
                            message:res.msg,
                            type:'warning'
                        });
                    }
                }).catch(() => {

                });
            },
            async jumpLink(url){
                if(this.login){
                    this.$router.push({ path: url });
                }else{
                    this.$message({
                        message: '',
                        type: 'warning'
                    })
                }
            },
            fresh(){
                this.$router.go(0);
            }
        }

    }

</script>

<style scoped>
    .topBar{
        height:36px;
        line-height: 36px;
        background-color:#f2f2f2;
        color:#666;
        border-bottom:1px solid #e0e0e0;
        font-size:14px;
    }
    .navigation-wrap{background-color: #fff;}
   .tm_item span, .tm_item a{
       font-size: 14px;
        line-height: 14px;
        padding:0 15px;
        border-right:1px solid #e0e0e0;
    }
    .tm_item span:hover{
        cursor: pointer;
        color:#0BB8B2;
    }
    .logo img{
        height:90px;
    }
    .badge{
        background-color: #f56c6c;
        border-radius: 10px;
        color: #FFF;
        display: inline-block;
        font-size: 12px;
        height: 18px;
        line-height: 18px;
        padding: 0 6px;
        text-align: center;
        white-space: nowrap;
        border: 1px solid #FFF;
    }
    /*.logoTit{*/
    /*    font-size:28px;*/
    /*    font-weight:blod;*/
    /*    line-height: 100px;*/
    /*    border-left: 1px solid #f0f0f0;*/
    /*    padding-left: 30px;*/
    /*    margin-left: 30px;*/
    /*    color:#333;*/
    /*}*/
    .navItem{
        line-height: 100px;
        font-size:16px;
        font-weight: bold;
        padding:0 20px;
    }
    .navItem a{
        color:#333;
        padding:10px;
    }
    .navItem.active a{
        border-bottom:2px solid #0bb8b2;
    }
    .navLabel{
        font-size:12px;
        font-weight: normal;
        color:#fff;
        background-color:#0bb8b2;
        padding:1px 3px;
        width:28px;
        height:16px;
        text-align: center;
        line-height: 16px;
        position: absolute;
        top:20px;
        right:-5px;
    }
    .navItemLast{
        padding-top:30px;
        text-align: right;
        font-size: 14px;
    }
    .navItemLast span{
        color:#0bb8b2;
        font-weight:bold;
    }
    .navItemLast p{
        color:#999;
    }
    .borderLine{
        border-left: 1px solid #eee;
    }

</style>



